<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  @import '../style/index.scss';
  .main{
    width:100%;
    height:100%;
    min-height:6rem;
    .navBar{
      //display: flex;
      ul{
        //flex:1;
        margin:0 auto;
        width:50%;
        display:flex;
      }
      li{
        flex:0.33;
        display: inline-block;
        cursor: pointer;
        min-width: 1.3rem;
        font-size:0.24rem;
        font-weight:700;
      }
      li:hover{
        @extend .animated;
        @extend .fadeIn;
        animation-duration:2s;
      }
      .title{
        font-size:1rem;
        margin-top:0.5rem;
        margin-bottom:1rem;
      }
    }
